import React, { ReactNode, useEffect, useState } from 'react';
import { Icon } from '@ipf-dev/sds-icon';
import {
  Container,
  IconButton,
  NumberButton,
  NumberContainer,
} from './Pagination.style';

/**
 *
 * @prop {number} currentPage -   1 
 * @prop {number} totalPage -   1   null 
 * @prop {() => void} onPrevClick -  (<)     
 * @prop {() => void} onNextClick -  (>)     
 * @prop {(pageNum: number) => void} onPageNumClick -       
 * @returns
 */
export default function Pagination({
  currentPage,
  totalPage,
  // numberOfPageButtons = 9,
  onPrevClick,
  onNextClick,
  onPageNumClick,
}: {
  currentPage: number;
  totalPage: number;
  // numberOfPageButtons?: number;
  onPrevClick: () => void;
  onNextClick: () => void;
  onPageNumClick: (pageNum: number) => void;
}) {
  //         
  //   5,   9            5, 9  
  //       numberOfPageButtons prop             
  const [numberOfPageButtons, setNumberOfPageButtons] = useState(9);

  useEffect(() => {
    const resizeHandler = () => {
      const width = window.innerWidth;

      if (width <= 480) {
        setNumberOfPageButtons(4);
      } else if (width <= 767) {
        setNumberOfPageButtons(5);
      } else {
        setNumberOfPageButtons(9);
      }
    };
    window.addEventListener('resize', resizeHandler);
    resizeHandler();

    return () => {
      window.removeEventListener('resize', resizeHandler);
    };
  }, []);

  if (totalPage <= 1) {
    return null;
  }

  const numberButtons: ReactNode[] = [];
  if (totalPage <= numberOfPageButtons) {
    for (let i = 1; i <= totalPage; i++) {
      numberButtons.push(
        <NumberButton
          key={i}
          isSelected={i === currentPage}
          onClick={() => onPageNumClick(i)}
        >
          {i}
        </NumberButton>,
      );
    }
  } else {
    //    

    //      1
    let startIndex = currentPage - Math.floor(numberOfPageButtons / 2);
    if (startIndex < 1) {
      startIndex = 1;
    }

    //      totalPage
    let endIndex = currentPage + Math.floor(numberOfPageButtons / 2);
    if (totalPage < endIndex) {
      endIndex = totalPage;
    }
    if (endIndex !== totalPage && numberOfPageButtons % 2 === 0) {
      endIndex -= 1;
    }

    for (let i = startIndex; i <= endIndex; i++) {
      numberButtons.push(
        <NumberButton
          key={i}
          isSelected={i === currentPage}
          onClick={() => onPageNumClick(i)}
        >
          {i}
        </NumberButton>,
      );
    }

    //        
    if (numberButtons.length < numberOfPageButtons) {
      if (startIndex === 1) {
        //       ,  
        let i = endIndex + 1;
        while (numberButtons.length !== numberOfPageButtons) {
          const pageNum = i;
          numberButtons.push(
            <NumberButton
              key={pageNum}
              isSelected={pageNum === currentPage}
              onClick={() => onPageNumClick(pageNum)}
            >
              {pageNum}
            </NumberButton>,
          );
          i += 1;
        }
      } else if (endIndex === totalPage) {
        //       ,  
        let i = startIndex - 1;
        while (numberButtons.length !== numberOfPageButtons) {
          const pageNum = i;
          numberButtons.unshift(
            <NumberButton
              key={pageNum}
              isSelected={pageNum === currentPage}
              onClick={() => onPageNumClick(pageNum)}
            >
              {pageNum}
            </NumberButton>,
          );
          i -= 1;
        }
      }
    }
  }

  return (
    <Container>
      <IconButton
        type="button"
        disabled={currentPage === 1}
        onClick={onPrevClick}
        title="prev"
      >
        <Icon.Arrow
          styleType="chevron"
          direction="left"
          disabled={currentPage === 1}
        />
      </IconButton>

      <NumberContainer>
        {numberButtons.map((element) => element)}
      </NumberContainer>

      <IconButton
        type="button"
        disabled={currentPage === totalPage}
        onClick={onNextClick}
        title="next"
      >
        <Icon.Arrow
          styleType="chevron"
          direction="right"
          disabled={currentPage === totalPage}
        />
      </IconButton>
    </Container>
  );
}
